}

.main-nav__site-link {
  text-decoration: none;
  color: #ffffff;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .main-nav__site-link {
    font-size: 14px;
    line-height: 26px;
    color: rgb(51, 51, 51);
    font-weight: 700;
  }
  .main-nav__site-link:hover {
    color: #81b3d2;
  }
  .main-nav__site-link:active {
    color: #dbdbdb;
    outline: none;
  }
  .main-nav__site-link:focus {
    color: #756161;
    outline: none;
  }
}

.main-nav {
  position: relative;
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    background-color: #ffffff;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px fade(#000000, 40%);
  }
  .main-nav--closed .main-nav__site-list {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  position: absolute;
  border: none;
  background-image: url("../img/header/icon-menu-open.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  height: 23px;
  width: 22px;
  top: 17px;
  right: 20px;
  background-color: transparent;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle:hover {
  opacity: 0.6;
}

.main-nav__toggle:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__site-list {
    position: static;
    display: flex;
    flex-direction: column;
  }
}

.main-nav--nojs {
  z-index: 2;
}

.main-nav--closed .main-nav__toggle {
  position: absolute;
  border: none;
  background-image: url("../img/header/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  height: 23px;
  width: 22px;
  top: 17px;
  right: 20px;
  background-color: transparent;
  z-index: 5;
}
@media (min-width: 768px) {
